.navigation {
    display: flex;
    font-size: 1.5rem;
    line-height: 2.8rem;
    &__link {
        list-style: none;
        padding: 0 10px;
        cursor: pointer;
        @extend %transition;
        &:hover {
            color:  $color-primary;
        }
    }
}

.header__navigation {
    @include media-tablet {
        display: none;
    }
}