%transition {
    transition: all 0.3s;
}

%font-primary {
    font-family: "Open Sans", Verdana, sans-serif;
}

%font-add {
    font-family: "Poppins", Verdana, sans-serif;
}

%custom-scroll {
    &::-webkit-scrollbar {
      width: 5px;
    }
    &::-webkit-scrollbar-track {
      box-shadow: inset 0 0 1px grey;
    }
    &::-webkit-scrollbar-thumb {
      background: $color-violet;
      border-radius: 10px;
    }
  }
